:root {

  --primary: red;
  --secondary: purple;
  --light: white;
  --light-alt: grey;
  --dark: black;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira sans', sans-serif;
}

body {
  color: var(--light);
  background-color: var(--light-alt);
}

.app {

  padding: 32px;
}

h1 {
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 32px;
}

.todo {
  position: relative;
  background-color: var(--dark);
  padding: 16px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  transform: 0.5s;
  cursor: pointer;
  width: 50%;
  margin: 16px auto;
}

.todo:hover {
  opacity: 0.8;
}

.todo .checkbox {
  width: 20px;
  height: 20px;
  margin-right: 16px;
  border-radius: 999px;
  background-color: var(--light);
  transition: 0.4s;
}

.todo.is-complete .checkbox {
  background-color: var(--primary);
  background-image: linear-gradient(to bottom, var(--primary), var(--secondary));
}

.todo .text {
  font-size: 20px;
}


.todo.is-complete .text {
  text-decoration: line-through;
}

.todo .delete-todo {
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);

  width: 24px;
  height: 24px;
  color: var(--light);
  background-color: red;
  border-radius: 50%;


  display: flex;
  align-items: center;
  justify-content: center;
}

.todo .update-todo {
  position: absolute;
  top: 50%;
  right: 50px;
  transform: translateY(-50%);

  width: 75px;
  height: 24px;
  color: var(--light);
  background-color: red;
  border-radius: 10px;


  display: flex;
  align-items: center;
  justify-content: center;
}

.addPop {
  position: fixed;
  bottom: 32px;
  right: 32px;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 64px;
  height: 64px;
  border-radius: 999px;
  font-size: 28px;
  font-weight: 900;
  color: var(--light);
  background-image: linear-gradient(to bottom, var(--primary), var(--secondary));
  cursor: pointer;
}


.popup {

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 50%);

  width: 100%;
  max-width: 400px;
  background-color: var(--light);
  padding: 32px;
  border-radius: 16px;
  box-shadow: 0px 3px 24px var(--dark);

}

.closePopup {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 20px;
  height: 20px;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: black;
  background-color: var(--primary);
}

.add-todo-input {
  appearance: none;
  border: none;
  outline: none;
  background-color: azure;
  padding: 16px;
  border-radius: 16px;
  box-shadow: 0px 2px 24px rgba(0, 0, 0, 0.2);
  width: 100%;
  font-size: 20px;

}

.button {

  padding: 16px 32px;
  border-radius: 99px;
  background-image: linear-gradient(to right, var(--primary), var(--secondary));
  display: inline-block;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 18px;
  text-align: center;
  cursor: pointer;
}